import React from 'react'
import { Link } from 'gatsby'
import './PostLink.css'

const PostLink = ({ post }) => (
  <div>
    <Link className="postLink" to={post.frontmatter.path}>
      <div>
        <h3>{post.frontmatter.title}</h3>
        <p>{post.frontmatter.date}</p>
      </div>
      <div className="arrow">‚Üí</div>
    </Link>
  </div>
)

export default PostLink
